import React from 'react'

import Trainer from "../assets/trainer.png"
import Donato from "../assets/donato.png"
import Simone from "../assets/simone.png"
import Hedrick from "../assets/hedrick.png"
import Giuseppe from "../assets/giuseppe.png"
import ReactAudioPlayer from 'react-audio-player'

import PokeballSound from "../assets/pokeball.mp3"
import CatchedSound from "../assets/captured.mp3"

const pokemon = [
    {
        name: "Donato",
        img: Donato,
        lv: 30
    },
    {
        name: "Simone",
        img: Simone,
        lv: 1
    },
    {
        name: "Giuseppe",
        img: Giuseppe,
        lv: 99
    },
    {
        name: "Hedrick",
        img: Hedrick,
        lv: 8
    }
]

const FancyText = ({ text }) => {
    const [placeholder, setPlaceholder] = React.useState("");

    const index = React.useRef(0);

    function tick() {
        setPlaceholder(prev => prev + text[index.current]);
        index.current++;
    }
    React.useEffect(() => {
        if (index.current < text.length - 1) {
            setTimeout(tick, 50);
        }
    }, [placeholder]);

    return (placeholder
    )
}

class Battle extends React.Component {
    state = {
        splashMessage: "",
        pokemon: null,
        capturing: 0

    }
    catch = () => {
        setTimeout(() => {
            this.setState({ capturing: 1 })
            setTimeout(() => {
                this.setState({ capturing: 2 })
                setTimeout(() => {
                    this.setState({ capturing: 3 })
                    this.props.stopMusic();
                }, 6000)
            }, 2100)
        }, 4000)
    }
    componentDidMount() {
        const index = Math.floor(Math.random() * 4)
        this.setState({ pokemon: pokemon[index] })
        this.catch()
    }

    render() {
        const { pokemon, capturing } = this.state
        return pokemon && (
            <div className="battle">

                <div className="battle__top">
                    <div className="battle__pokemon battle__user">
                        <div className="battle__bar">
                            <div className="battle__name">{pokemon.name} <b>L {pokemon.lv}</b></div>
                            <div className="battle__hp">
                                <div className="hp__header">
                                    HP:
                                </div>
                                <div className="hp__bar" />
                            </div>
                        </div>
                        <img src={pokemon.img} className={capturing > 0 && "catching"} />
                    </div>
                    <div className="battle__trainer battle__user">
                        <div className="battle__bar">
                            <div style={{ display: "flex" }}>
                                <div className="battle__pokeball" />
                                <div className="battle__pokeball" />
                                <div className="battle__pokeball" />
                                <div className="battle__pokeball" />
                                <div className="battle__pokeball" />
                                <div className="battle__pokeball" />
                            </div>
                        </div>
                        <img src={Trainer} />
                    </div>
                </div>
                <div className="battle__action">
                    <div className="battle__text">
                        {capturing === 0 && <FancyText text={"A  wild " + pokemon.name + " appeared!"} />}
                        {(capturing === 1 || capturing === 2) && <FancyText text={"Goo, pokeball!"} />}
                        {capturing === 3 && <FancyText text={"Coongratulations, " + pokemon.name + " has been captured"} />}

                        {
                            capturing === 3 && <audio src={CatchedSound} autoPlay />
                        }
                        {
                            capturing === 1 && <audio src={PokeballSound} autoPlay />
                        }
                        {
                            capturing > 0 &&
                            <div className={`pokeball ${capturing > 0 && capturing < 3 ? "pokeball--throwing" : "pokeball--catched"}`} />
                        }
                    </div>
                </div>
            </div >
        )
    }
}

export default Battle;