import React from 'react'

class Background extends React.Component {
    state = {

    }
    render() {
        const height = 40

        return (
            <div className="background">
                {Array(parseInt(window.innerHeight / height)).fill(0).map((_, i) =>
                    <div className="background__bar" key={i} style={{ height: height + "px" }} />
                )}
            </div>
        );
    }
}

export default Background;