import React from "react"
import Pokeball from "./assets/pokeball.png"
import Background from "./components/background";
import WildMusic from "./components/wild-music";
import Battle from "./components/battle";
import "./css/style.css"

class App extends React.Component {
  state = {
    start: false,
    showBackground: false,
    showBattle: false
  }
  stopMusic = () => {
    this.setState({ start: false })
  }
  start = () => {
    this.setState({ start: true, showBackground: true })
    setTimeout(() => this.setState({ showBackground: false, showBattle: true }), 3500)
  }

  render() {
    const { start, showBackground, showBattle } = this.state
    return (
      <div className="container">
        {showBackground && <Background />}
        {start && <WildMusic />}
        {showBattle ? <Battle stopMusic={this.stopMusic} />
          :
          <div className="splash">
            <img onClick={this.start} className="splash__image" src={Pokeball} />
          </div>}
      </div>
    )
  }
}

export default App;